<template>
    <div class="user-info" @mouseover="infoShow = true" @mouseleave="infoShow = false">
        <img src="../../../assets/userInfo/userImg.png" alt="" >
        <div class="info-list" v-show="infoShow">
            <el-button type="info" plain size="mini" @click="logout">切换账号</el-button>
            <el-button type="info" plain size="mini" @click="logout">退出登录</el-button>
        </div>
    </div>
</template>
<script>
import {removeToken,removeUserInfo} from "@/utils/auth";

export default {
    name: "UserInfo",
    data() {
      return {
          infoShow: false,
      }
    },
    methods: {
        logout() {
            removeToken();
            removeUserInfo();
            this.infoShow = false;
            //this.$router.push('login');
            this.$message({
                type: 'success',
                message: `恭喜您，退出账号成功！`
            });
            location.reload();
        }
    }
}
</script>

<style scoped lang="less">
.user-info {
    position: fixed;
    top: 5px;
    right: 60px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    z-index: 99;
    img {
        margin: 0;
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    .info-list {
        width: 80px;
        background-color: #e8e1e8;
        //margin-left: -30px;
        display: flex;
        flex-flow: column wrap;
        justify-content: flex-start;
        /deep/ .el-button+.el-button {
            margin-left: 0;
        }
    }
}

</style>