<template>
    <div class="aside-menu">
        <ul class="level-one-parent" @click="levelOneLinkTo">
            <li class="level-one-child" v-for="(item) in config" :key="item.id">
                <div class="row">
                    <span :data-path="item.path">{{ item.title }}</span>
                    <i v-if="!item.opened" class="el-icon-arrow-down"></i>
                    <i v-else class="el-icon-arrow-right"></i>
                </div>
                <ul v-if="item.children" class="level-two-parent">
                    <li v-for="(item2) in item.children" :key="item2.id">
                        <span :data-path="item.path+item2.path">{{ item2.title }}</span>
                    </li>
                </ul>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "aside-menu",
    props: {
        cfg: Array,
    },
    data() {
        return {
            config: [
                {
                    id: 3, title: '公司导航', path: '/navContent',
                },
                // {
                //     id: 2, title: '小组2', path: '/second',opened: false,
                //     children: [
                //         {id: 1, title: '小组2-1', path: '/89',},
                //         {id: 2, title: '小组2-2', path: '/10',}
                //     ]
                // },
            ],
        }
    },
    methods: {
        levelOneLinkTo(e) {
            //this.$parent.menuIcon = !this.$parent.menuIcon;
            //console.log(this.$parent.menuIcon)
            this.$emit('changeIcon')
            if (e.target.nodeName.toLowerCase() === 'span') {
                // let item = e.target.dataset.item;
                // console.log(item)
                let path = e.target.dataset.path;
                console.log(path);
                this.$router.push(path);
            }

            //this.$router.push(path);
        },
    },
    created() {
        //this.config = {...this.config, ...this.cfg};
        //this.config = this.cfg;
    }


}
</script>

<style scoped lang="less">
.aside-menu {
    width: 100%;
    height: 100%;
    z-index: 9999;
    .level-one-parent {
        width: 100%;
        height: 100%;
        display: flex;
        box-sizing: border-box;
        flex-flow: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding-left: 20px;
        padding-top: 25px;
        background-color: #4cc9f0;
        .level-one-child {
            display: flex;
            flex-flow: column;
            justify-content: flex-start;
            .row {
                display: block;
                border: 0;
                margin: 5px;
                text-transform: uppercase;
                font-weight: bold;
                padding: 2px 15px;
                border-radius: 50px;
                color: white;
                outline: none;
                position: relative;
                background-image: linear-gradient(to right, #6253e1, #852D91);
                box-shadow: 0 4px 15px 0 rgba(236, 116, 149, 0.75);
                &::before{
                    content: '';
                    display: block;
                    background: linear-gradient(to left, rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 0.4) 50%);
                    background-size: 210% 100%;
                    background-position: right bottom;
                    height: 100%;
                    width: 100%;
                    position: absolute;
                    top: 0;
                    bottom:0;
                    right:0;
                    left: 0;
                    border-radius: 50px;
                    transition: all 1s;
                    -webkit-transition: all 1s;
                }
                &:hover:before {
                    background-position: left bottom;
                }
            }
            .level-two-parent {
                margin-left: 20px;
            }
        }

    }
    li {
        cursor: pointer;
    }


}

</style>