<template>
    <div class="menu-control">
        <div class="menu-icon" @click="changeMenuIcon">
            <i v-if="menuIcon" class="el-icon-s-fold" style="color: white"></i>
            <i v-else class="el-icon-s-unfold" style="color: white"></i>
            <span style="font-size: 16px;color: #409EFF"></span>
        </div>
        <div class="app-main">
            <transition name="menu">
                <aside-menu class="aside-menu" v-show="menuIcon" @changeIcon="changeMenuIcon"></aside-menu>
            </transition>
            <el-scrollbar>
                <router-view></router-view>
            </el-scrollbar>
        </div>
    </div>
</template>

<script>
import {_debounce} from "@/utils/shared";
import AsideMenu from "@/views/NavContent/components/AsideMenu";
export default {
    name: "menu-control",
    components: {
        AsideMenu,
    },
    data() {
        return {
            menuIcon: false,
            wrapShow: false,
            labels: [],
        }
    },
    //监听路由更变
    watch: {
        '$route': 'routeUpdate'
    },
    methods: {

        showLabels() {
            this.wrapShow = !this.wrapShow;
        },
        changeMenuIcon:  _debounce(function () {
            this.menuIcon = !this.menuIcon;
        },200),
        routeUpdate(to) {
            console.log(to);
            //在页面刷新时将vuex里的信息保存到sessionStorage里
            window.addEventListener("beforeunload", () => {
                sessionStorage.setItem("pathName", window.location.pathname)//重点：暂存页面刷新的地址
            })
        },
    },
    created() {
        this.routeUpdate(this.$route);
    },

}
</script>

<style scoped lang="less">
/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.menu-enter-active {
    transition: all .3s ease;
}
.menu-leave-active {
    transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.menu-enter, .menu-leave-to{
    transform: translateX(10px);
    opacity: 0;
}
.menu-control {
    position: absolute;
    bottom: 0px;
    top: 0px;
    left: 0;
    right: 0;
    .menu-icon {
        position: fixed;
        top: 58px;
        left: 0px;
        font-size: 30px;
        font-weight: bolder;
        z-index: 99999;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .app-main {
        width: 100%;
        height: 100%;
        position: relative;
        .aside-menu {
            position: absolute;
            width: 200px;
            height: 100%;
            background-color: #409EFF;
        }
    }
}
</style>