<template>
    <div class="single-nav">
        <div class="nav-show">
            <div class="navTitle-tip">
                个人导航夹
                <span>(请从左侧拖入)</span>
            </div>
            <draggable
                    tag="el-collapse"
                    class="dragArea nav-show-wrap"
                    :list="list2"
                    group="comp"
                    @change="log"
            >
                <div v-for="(element,index) in list2" :key="index" class="single-wrap"
                     @click="linkTo(element.url)">
                    <i class="el-icon-close right-top" @click.stop="deleteItem(index)"></i>
<!--                    <div class="bgParent" v-if="element.url+ '/favicon.ico'">-->
<!--                        <img :src="getIconUrl(element.url)" alt="">-->

<!--                    </div>-->
                    <span>{{ element.title }}</span>
                </div>
            </draggable>

        </div>
        <div class="nav-pools">
            <el-scrollbar style="height: 100%;">
                <h4 style="background-color: #409EFF; color: white;height: 30px;vertical-align: center">系统可选导航</h4>
                <draggable
                        class="dragArea"
                        style="padding: 10px"
                        :list="list1"
                        :group="{ name: 'comp', pull: 'clone', put: true }"
                        @change="log"
                >
                    <el-tag :closable="element.closable" v-for="element in list1"
                            :key="element.id"
                            :type="tagsColor[parseInt(Math.random()*5,10)]"
                            class="tagClass"
                            effect="dark"
                            @close="handleClose(element.id)">
                        {{ element.title }}
                    </el-tag>
                </draggable>
            </el-scrollbar>
        </div>
        <div class="edit-btn">
            <edit-nav @addNavData="updateAllList($event)"></edit-nav>
        </div>
    </div>
</template>

<script>
import draggable from "vuedraggable";
import EditNav from "@/views/NavContent/components/EditNav";
import {getAll, updateAll} from "@/api/navLists"

export default {
    name: 'single-nav',
    components: {
        draggable,
        EditNav,
    },
    data() {
        return {
            tagsColor: ['', 'success', 'info', 'warning', 'danger'],
            list1: [
                {
                    title: "tuleap",
                    id: 1,
                    url: 'https://tp.deloitte.com.cn/account/login.php',
                },
                {
                    title: "Vue",
                    id: 2,
                    url: 'https://vuejs.bootcss.com/guide/'
                },
                {
                    title: "Vuex",
                    id: 3,
                    url: 'https://vuex.vuejs.org/zh/',

                },
                {
                    title: "Vue Router",
                    id: 4,
                    url: 'https://router.vuejs.org/zh/',
                },
                {
                    title: "JS教程",
                    id: 5,
                    url: 'https://wangdoc.com/javascript/',
                },
                {
                    title: "菜鸟教程",
                    id: 6,
                    url: 'https://www.runoob.com/css/css-tutorial.html',
                },
                {
                    title: "掘金",
                    id: 7,
                    url: 'https://juejin.im/',
                },
            ],
            list2: [],
        }
    },
    methods: {
        /**
        * @方法目的：  获取网站图标地址
        * @param {参数类型} 参数名 参数说明
        * @return {返回值类型} 返回值说明
         * document.querySelectorAll('link[rel*="icon"]')[0].href
        */
        getIconUrl(url) {
            console.log(url);

        },

        /**
         * @方法目的：
         * @param {参数类型} 参数名 参数说明
         * @return {[]} 返回值说明
         */
        dropDouble(target) {
            let tempArray = [];
            target.forEach(item => {
                tempArray.push(item.id + '|' + item.title + '|' + item.url)
            });
            let arr = [...new Set(tempArray)];
            let finallyArray = [];
            arr.forEach(item => {
                let arr = item.split('|');
                let obj = {
                    id: arr[0],
                    title: arr[1],
                    url: arr[2]
                }
                finallyArray.push(obj)
            })
            return finallyArray;
        },
        handleClose(tag) {
            this.list1.splice(this.list1.indexOf(tag), 1);
            this.updateAllList();
        },
        linkTo(path) {
            window.open(path);
        },
        log(evt) {
            this.list2 = this.dropDouble(this.list2);
            console.log(evt);
            console.log(this.list2, '99999999');
            this.updateAllList();

        },
        deleteItem(index) {
            this.list2.splice(index, 1);
            this.updateAllList();
        },
        /**
         * @方法目的： 获取可选导航数据
         * @param {参数类型} 参数名 参数说明
         * @return {返回值类型} 返回值说明
         */
        getAllLists() {
            let _self = this;
            //console.log('执行了获取数据')
            console.log(this.$store.state.user.userInfo,'000000')
            getAll({userid: this.$store.state.user.userInfo}).then(res => {
                //console.log(res.data.data[0]['list'],'9999999999')
                let lists = JSON.parse(res.data.data[0]['list']);
                //console.log(lists,'88888')
                if (lists.length === 2) {
                    _self.list2 = lists[1];
                }
                console.log(lists[0], '看一下从后台获取的数据666666')
                if (lists[0].length > 7) {
                    this.list1 = lists[0];
                }
                // eslint-disable-next-line no-unused-vars
            }).catch(err => {
                //console.log(err);
            })
        },
        updateAllList(data) {
            let _self = this;
            if (data) {
                let len = this.list1.length;
                data.id = len + 1;
                data.closable = true;
                _self.list1 = _self.list1.concat(data);
            }
            let list = [this.list1, this.list2];
            //对上传数据库的数据进行处理
            console.log(list, '看一下data');
            let upData = {
                list: JSON.stringify(list),
                userid: this.$store.state.user.userInfo
            };
            console.log(upData, '77777')
            updateAll(upData).then(res => {
                console.log(res, '更新后返回的数据');
                _self.getAllLists();
            }).catch(err => {
                console.log(err)
            })
        }

    },
    created() {
        this.getAllLists();
    },
    mounted() {
    }

}
</script>

<style>
.zy-main {
    height: 100%;
    overflow: hidden;
}

.el-scrollbar {
    height: 100%;
}

.el-scrollbar__wrap {
    overflow: scroll;
    overflow-x: auto
}
</style>
<style scoped lang="less">
.single-nav {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 200px;
    right: 200px;

    .nav-show {
        width: 100%;
        height: 100%;
        background-color: #e7e8ef;
        padding-top: 30px;
        position: relative;

        .navTitle-tip {
            position: absolute;
            height: 30px;
            width: 100%;
            line-height: 30px;
            font-size: 20px;
            color: white;
            background-color: #409EFF;
            top: 0;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 0);

            span {
                font-size: 16px;
            }
        }

        .nav-show-wrap {
            display: flex;
            flex-flow: row wrap;
            justify-content: flex-start;
            align-items: flex-start;
            width: 100%;
            height: 100%;

            .single-wrap {
                border: 1px solid transparent;
                display: flex;
                flex-flow: column;
                position: relative;
                width: 80px;
                height: 80px;
                margin: 10px;
                border-radius: 15px;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                //background-color: transparent;
                background-color: #a7e8d6;
                .bgParent {
                    width: 55px;
                    height: 55px;
                    img {
                        width: 100%;
                    }
                }
                span {
                    font-size: 16px;
                    //font-weight: bolder;
                }

                .right-top {
                    position: absolute;
                    top: 0;
                    right: 0;
                    cursor: pointer;
                    font-size: 23px;

                    &:hover {
                        color: red;
                    }
                }

                &:hover {
                    color: white;
                    background-color: #4cc9f0;
                    -webkit-box-shadow: 10px 10px 99px 6px rgba(76, 201, 240, 1);
                    -moz-box-shadow: 10px 10px 99px 6px rgba(76, 201, 240, 1);
                    box-shadow: 10px 10px 99px 6px rgba(76, 201, 240, 1);
                }
            }
        }
    }

    .nav-pools {
        position: absolute;
        //padding-top: 10px;
        top: 0;
        left: -200px;
        width: 200px;
        height: 100%;
        .tagClass {
            margin:3px;
            cursor: pointer;
            &:hover {
                border-radius: 50px 10px 50px 10px;
            }
        }
        //border: 1px solid #a7dbe8;
    }

    .edit-btn {
        position: absolute;
        padding-top: 10px;
        top: 0;
        right: -200px;
        width: 200px;
        height: 100%;
    }


}

</style>

