<template>
    <div class="edit-nav">
        <el-button @click="drawer = true" type="primary" style="margin-left: 16px;">
            添加系统导航 <i class="el-icon-plus el-icon--right"></i>
        </el-button>
        <el-drawer
                title="我是标题"
                :visible.sync="drawer"
                :with-header="false">
            <div class="edit-content">
                <span>请编辑您的导航信息</span>
                <div class="edit-text">
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="名称：" prop="title">
                            <el-input v-model="ruleForm.title" clearable></el-input>
                        </el-form-item>
                        <el-form-item label="地址：" prop="url">
                            <el-input v-model="ruleForm.url" clearable></el-input>
                        </el-form-item>
<!--                        <el-form-item label="分类：" prop="label">-->
<!--                            <el-select v-model="ruleForm.label" placeholder="请选择所属分类">-->
<!--                                <el-option :label="item" :value="item" v-for="(item,index) in labels" :key="index"></el-option>-->
<!--                            </el-select>-->
<!--                        </el-form-item>-->
                        <el-form-item>
                            <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
                            <el-button @click="resetForm('ruleForm')">重置</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
        </el-drawer>
    </div>
</template>

<script>


export default {
    name: "edit-nav",
    data() {
        return {
            drawer: false,
            ruleForm: {
                'title': '',
                'url': '',
                'id': null
            },
            rules: {
                title: [
                    { required: true, message: '请输入导航名称', trigger: 'blur' },
                    { min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }
                ],
                url: [
                    { required: true, message: '请输入链接地址', trigger: 'blur' },
                    { min: 1, max: 35, message: '长度在 1 到 35 个字符', trigger: 'blur' }
                ],
            },

        }
    },
    methods: {
        submitForm(formName) {
            let _self = this;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // todo 这里可以使用一个单例模式
                    this.$message({
                        type: 'success',
                        message: '恭喜您，添加导航成功'
                    });
                    if(_self.ruleForm.url.includes('https://') || _self.ruleForm.url.includes('http://')) {
                        console.log('检测地址正确')
                    }else {
                        _self.ruleForm.url = 'https://' + _self.ruleForm.url;
                    }
                    _self.$emit('addNavData',_self.ruleForm)
                    this.drawer = false;
                    _self.resetForm(formName);
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    },

}
</script>

<style scoped lang="less">
.edit-nav {
    .edit-content {
        width: 100%;
        height: 100%;
        padding: 40px 20px;
       .edit-text {
           margin-top: 50px;
           /deep/ .el-select {
               width: 100%;
           }
       }
    }
}

</style>